import React from "react"
import { Link } from "gatsby"

import { rhythm, scale } from "../utils/typography"
import '@src/styles/global.scss'
class Layout extends React.Component {
  render() {
    const { location, title, children } = this.props
    const rootPath = `${__PATH_PREFIX__}/`
    let header

    header = (
      <h1
      className='title'
        style={{
          ...scale(0.5),
          padding: `${rhythm(0.3)} 0`,
          marginTop: 0,
        }}
      >
      <i className="nes-logo"></i>
        <Link
          style={{
            boxShadow: `none`,
            textDecoration: `none`
          }}
          to={`/`}
        >
          {title}
          <span className='blog'>'s Blog</span>
        </Link>
      </h1>
    )
    return (
      <div
        style={{
          
          
        }}
      >
        <header className='headerNav'>{header}</header>
        <main
        className='container'
        style={{
          marginLeft: `auto`,
          marginRight: `auto`,
          maxWidth: rhythm(40),
        }}>{children}</main>
        <footer>
          © 2019  «SanJin»
          {` `}
          powered by <a href="https://www.gatsbyjs.org">Gatsby</a>
        </footer>
      </div>
    )
  }
}

export default Layout
